#landing {
	display: flex;
	flex-direction: column;
	background: url('./pexels-adrien-olichon-2387793.jpg');
	background-size: cover;
	min-height: 100vh;
	justify-content: center;
	align-items: center;
	text-align: center;
	width: 100%;
}

#name {
	display: inline-block;
	background: url('./pexels-innermost-limits-3894157.jpg');
	background-clip: text;
	color: transparent;
	font-size: 4rem;
	font-weight: 900;
}

#back-img {
	width: 100%;
}

#typewriter {
	color: white;
	font-size: 1.5rem;
	font-weight: bold;
}

#typewriter-text {
	font-weight: normal;
	color: whitesmoke;
}

#typewriter-end {
	padding-left: 5px;
	animation: typewriter-end .75s step-end infinite;
}

@keyframes typewriter-end {
	from,
	to {
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
}

@media only screen and (min-width: 1024px) {
	#typewriter {
		font-size: 3rem;
		text-shadow: rgba(0, 0, 0, 0.568) 4px 4px 5px, rgba(255, 255, 255, 0.144) -4px -4px 5px;
	}

	#name {
		font-size: 8rem;
	}
}
